.messages {
  margin: 0;
  margin-bottom: 20px;
  transform: all 0.5 ease;
}

.parrot-message {
  width: 50px;
}

.message-bubble {
  background-color: #001487;
  border-radius: 8px;
  color: #fff;
  margin-left: 7px;
  padding: 10px;
  position: relative;
  font-size: 14px;
  margin-top: -18px;
}

.message-bubble::before {
  background-color: #001487;
  border-radius: 0 0 0 3px;
  content: "";
  height: 12px;
  left: -3px;
  margin: 0 auto;
  position: absolute;
  top: calc(50% - 6px);
  transform: rotate(45deg);
  width: 12px;
}

.errornote li {
  color: #c90000;
  font-size: 0.75rem;
  text-align: left;
  padding: 8px;
  margin-bottom: 8px;
  border-radius: 50px;
}

